import React from "react"
import {graphql, useStaticQuery} from "gatsby"

import ExternalLink from "../ExternalLink"

const TwitterContact = ({title = "Twitter"}) => {
  const {
    site: {
      siteMetadata: {twitterContact},
    },
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          twitterContact
        }
      }
    }
  `)

  return <ExternalLink href={twitterContact} title={title} />
}

export default TwitterContact
