import React from "react"

const ExternalLink = ({href, title, children, className = ""}) => {
  if (!children) {
    children = <span>{title}</span>
  }

  return (
    <a
      href={href}
      title={title}
      className={className}
      rel="noopener noreferrer"
      target="_blank">
      {children}
    </a>
  )
}

export default ExternalLink
